
export class Brief {
  name
  get Name() {
    if (!this.name) {
      this.name = 'class name'
    }
    return this.name;
  } set Name(v) {
    this.name = v;
  }

  description
  get Description() {
    if (!this.description) {
      this.description = 'm t'
    }
    return this.description;
  } set Description(v) {
    this.description = v;
  }
}