import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Popover } from 'antd';
import './CourseDict.css'
import * as util from './CourseDictVMUtil'

class CourseDictView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = util.GetTest()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='cd__wrapper'>
          {data.Items?.map(x => data.RenderItem(x))}
        </div>
      </>
    );
  }
}

export default observer(CourseDictView);
