import * as api from '../../../../util/apiUtil'
import { User } from './User';
import { url } from './User_Url'

// Instance
export const get = (dict) => {
  var qI = new User();

  qI.Dict = dict;

  return qI;
}

export const GetTest = (dict) => {
  var qI = get(dict);
  qI.Name = 'Trung'
  return qI;
}

export const GetTest1 = (dict) => {
  var qI = get(dict);
  qI.Name = 'Ti'
  return qI;
}

export const GetTest2 = (dict) => {
  var qI = get(dict);
  qI.Name = 'Ton'
  return qI;
}

export const GetTest3 = (dict) => {
  var qI = get(dict);
  qI.Name = 'Tng'
  return qI;
}

// Property


// api
export const GetData = (apiData, handle) => {
  apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}