import * as api from '../../../../../util/apiUtil'
import { Cost } from './Cost';
import { url } from './CostUrl'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = new Cost();

  qI._id = data._id;
  qI.Value = data.value;
  qI.Dict = dict;

  return qI;
}

export const get = (dict) => {
  var qI = new Cost();

  qI.Dict = dict;

  return qI;
}

// Property


// api
export const GetData = (apiData, handle) => {
  apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}