import { CourseVM } from "./CourseVM"
import { Button } from "antd";
import * as genUtil from '../../../../../util/generalUtil';
import * as infoUtil from '../info/base/InfoVMUtil'
import * as costUtil from '../cost/CostVMUtil'
import { MainVM } from "../main/base/MainVM";
import * as productDictUtil from '../product/dict/DictVMUtil'

// Instance
export const get = (q, dict) => {
  if (!q) return q;
  var qI = new CourseVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const Handle_Info = (q) => {
  if (q.IsGet_Info) return undefined;
  q.IsGet_Info = true;

  const setObj = (data) => {
    q.Info = infoUtil.get(data, q);
    q.IsGet_Info = false
  }

  var modelObj = q.ModelItem
  if (modelObj.Info){
    setObj(modelObj.Info)
  } else {
    genUtil.Add_2(
    modelObj.OnSet_Info_Func,
    () => setObj(modelObj.Info),
    false)
  }
}

export const GetCost = (q) => {
  return costUtil.get(q.ModelItem.Cost, q);
}

export const GetMain = (q) => {
  var qI = new MainVM();
  qI.Dict = q;
  return qI;
}

export const GetProductDict = (q) => {
  return productDictUtil.get(q.ModelItem.ProductDict, q)
}