import { makeObservable, observable, action } from 'mobx';
import * as util from './DescriptionVMUtil'
import HtmlRenderVM from '../../../../../html/HtmlRenderVM'

export class DescriptionVM {
  constructor() {
    makeObservable(this, {

    })
  }

  get Header(){
    return this.ModelItem.Header;
  } set Header(v){
    this.ModelItem.Header = v
  }

  get HtmlContent(){
    return this.ModelItem.HtmlContent;
  } set HtmlContent(v){
    this.ModelItem.HtmlContent = v
  }

  htmlContentVM
  get HtmlContentVM(){
    if (!this.htmlContentVM){
      this.htmlContentVM = util.GetHtmlContentVM(this)
    } return this.htmlContentVM;
  }
}