import { makeObservable, observable, action } from 'mobx';
import * as util from './CostVMUtil'

export class CostVM {
  constructor() {
    makeObservable(this, {
      rawValue : observable,
      realValue : observable
    })
  }

  rawValue = undefined
  get RawValue(){
    if (!this.rawValue){
      this.rawValue = this.ModelItem.RawValue
    } return this.rawValue;
  } set RawValue(v){
    this.rawValue = this.ModelItem.RawValue = v
  }

  realValue = undefined
  get RealValue(){
    if (!this.realValue){
      this.realValue = this.ModelItem.RealValue
    } return this.realValue;
  } set RealValue(v){
    this.realValue = this.ModelItem.RealValue = v;
  }

  FormatCurrency = (v) => {
    return new Intl.NumberFormat('de-DE', { style: 'currency', currency: 'VND' }).format(v);
  }

  get Discount(){
    return (1 - this.RealValue / this.RawValue)
  }

  FormatDiscount = (v) => {
    return Math.ceil(this.Discount * 100) + '%'
  }
}