import { makeObservable, observable, action } from 'mobx';
import * as util from './BenefitVMUtil'

export class BenefitVM {
  constructor() {
    makeObservable(this, {

    })
  }

  get Header(){
    return this.ModelItem.Header;
  } set Header(v){
    this.ModelItem.Header = v
  }

  get Items(){
    return this.ModelItem.Items;
  } set Items(v){
    this.ListView.Items = v;

    this.ModelItem.Items = v
  }

  listView
  get ListView(){
    if (!this.listView){
      this.listView = util.GetListView(this)
    } return this.listView
  }
}