import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Popover } from 'antd';
import './Info.css'
import { InfoVM } from './InfoVM';
import BriefView from '../brief/BriefView';
import BenefitView from '../benefit/BenefitView';
import DescriptionView from '../description/DescriptionView';
import ParticipantView from '../participant/ParticipantView';

class InfoView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new InfoVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='ci__wrapper'>
          <div className='ci__b'>
            <BriefView store={data.Brief} />
          </div>
          <div className='flex-center'>
            <div className='w80'>
              <div className='w65'>
                <div className='ci__be offset'>
                  <BenefitView store={data.Benefit} />
                </div>
                <div className='ci__d offset'>
                  <DescriptionView store={data.Description} />
                </div>
                <div className='ci__p offset'>
                  <ParticipantView store={data.Participant} />
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default observer(InfoView);
