import * as m_it from '../base/Main_ViewType'
import * as menuItemUtil from '../../../../../nav/item/ItemUtil'
import { Dict as ItemDict } from '../../../../../nav/dict/Dict'

// Property
export const GetItemDict = (q) => {
  var qI = new ItemDict();

  qI.WrapperCss = 'cm__main__menu__itemDict'
  qI.ItemCss = 'cm__main__menu__item main-bgcolor'

  return qI;
}

export const GetItems = (q) => {
  var list = [m_it.content, m_it.student_product]
  var dict = q.ItemDict;

  const getItem = (value) => {
    var qI = menuItemUtil.get(dict);
    
    qI._id = value
    qI.Title = m_it.GetTitle(value)
    qI.Func = () => q.Dict.ViewType = value

    return qI;
  }

  return list.map(x => getItem(x));
}

export const SetItems = (q) => {
  var items = GetItems(q);
  var qI = q.ItemDict;
  qI.Items = items;

  items[0].Active();
}