import { makeObservable, observable, action } from 'mobx';
import { PMData } from '../model/PMData';
import * as util from './PMDataVMUtil'
import * as genUtil from '../../../../util/generalUtil'
import { Route, BrowserRouter, HashRouter, Switch } from 'react-router-dom';
import LayoutView from '../../entity/layout/layout/LayoutView';
import PMDataView from './PMDataView';
import * as vt from '../../entity/view/ViewType'

export class PMDataVM {
  constructor() {
    makeObservable(this, {
      isAdmin: observable,
      CurrentCourse : observable,
      RouteData : observable
    })
  }

  static instance
  static get Instance() {
    if (!this.instance) {
      this.instance = util.get();
    }
    return this.instance;
  } static set Instance(v) {
    this.instance = v
  }

  get pmData() {
    return PMData.Instance;
  }

  // Layout
  layout
  get Layout() {
    if (!this.layout) {
      this.layout = util.GetLayout(this)
    }
    return this.layout
  }

  OnSet_IsAdmin_Func_Dict = []

  // User
  isAdmin = undefined
  get IsAdmin() {
    return this.isAdmin
  } set IsAdmin(v) {
    this.isAdmin = v;

    genUtil.Handle(this.OnSet_IsAdmin_Func_Dict,
      (v) => this.OnSet_IsAdmin_Func_Dict = v)
  }

  // Route
  RouteData = undefined

  // View
  // ViewType = vt.home

  get MainView() {
    return <PMDataView store={this}/>
  }

  get WrapperView() {
    return this.pmData.IsDeploy ?
      <HashRouter basename={process.env.PUBLIC_URL}>
        <Switch>
          {this.MainView}
        </Switch>
      </HashRouter>
      :
      <BrowserRouter>
        <Switch>
          {this.MainView}
        </Switch>
      </BrowserRouter>
  }

  // Data
  // Course
  courseDict
  get CourseDict() {
    if (!this.courseDict) {
      this.courseDict = util.GetCourseDict(this)
    } return this.courseDict
  }

  CurrentCourse = undefined
}