import { message } from 'antd';
import * as genUtil from '../../../../../../util/generalUtil'
import { makeObservable, observable, action } from 'mobx';
import * as util from './MainVMUtil'
import * as vt from './Main_ViewType'
import MainView from './MainView';

export class MainVM {
  constructor() {
    makeObservable(this, {
      ViewType: observable
    })
  }

  menu
  get Menu() {
    if (!this.menu) {
      this.menu = util.GetMenu(this)
    } return this.menu
  }

  ViewType = vt.content

  get View() {
    return <MainView store={this} />
  }
}