import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Popover } from 'antd';
import './CourseCost.css'
import { CourseVM } from '../../base/CourseVM';
import CostView from '../../cost/CostView';

class CourseItemView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new CourseVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='coursecost__wrapper'>
          {data.ThumbnailView}
          <div className='coursecost__main__wrapper'>
            <CostView store={data.Cost}/>
          </div>
        </div>
      </>
    );
  }
}

export default observer(CourseItemView);
