import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { MenuVM } from './MenuVM';
import './Menu.css'

class MenuView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MenuVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='f'>
          {data.ItemDict.View}
        </div>
      </>
    );
  }
}

export default observer(MenuView);
