import { CourseDictVM } from "./CourseDictVM"
import { Button } from "antd";
import * as genUtil from '../../../../../util/generalUtil';
import * as mUtil from './CourseDictUtil'
import * as iUtil from '../base/CourseVMUtil'
import CourseItemView from "../view/item/CourseItemView";

// Instance
export const get = (q, dict) => {
  if (!q) return q;
  var qI = new CourseDictVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

export const GetTest = (mdict, dict) => {
  return get(mUtil.GetTest(mdict), dict);
}

// Property
// Course
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => iUtil.get(x, q))
    q.IsGet_Items = false
  }

  var modelObj = q.ModelItem
  if (modelObj.Items) {
    setObj(modelObj.Items)
  } else {
    genUtil.Add_2(
      modelObj.OnSet_Items_Func_Dict,
      () => setObj(modelObj.Items),
      false)
  }
}

export const GetDefault_RenderItem = (q) => {
  return (v) => <CourseItemView key={v.id} store={v} />
}