import { Button } from "antd";
import { DictVM } from "./DictVM";
import * as itemUtil from '../base/ProductVMUtil'

// Instance
export const get = (q, dict) => {
  if (!q) return q;
  var qI = new DictVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetItems = (q) => {
  return q.ModelItem.Items.map(x => itemUtil.get(x, q));
}