import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { ProductVM } from './ProductVM';
import './Product_Brief.css'

class Product_BriefView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new ProductVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className={`f-ac cpbr__wrapper ${data.ActiveCss}`}
          onClick={data.OnClick}>
          <div className='f1 cpbr__title__wrapper'>
            {data.Title}
          </div>

          <div className='f'>
            {/* Authors */}
            <div className='f-ac'>
              <span>Tc gi:</span>
              <div className='cpbr__users author f'>
                {data.Authors.map(x => x.BadgeView)}
              </div>
            </div>

            {/* Instructors */}
            <div className='f-ac'>
              <span>Hng dn:</span>
              <div className='cpbr__users instructor f'>
                {data.Instructors.map(x => x.BadgeView)}
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default observer(Product_BriefView);
