import * as util from './InfoUtil'
import { url } from './InfoUrl'

export class Info {
  brief
  get Brief(){
    if (!this.brief){
      this.brief = util.GetBrief(this)
    } return this.brief;
  } set Brief(v){
    this.brief = v;
  }

  benefit
  get Benefit(){
    if (!this.benefit){
      this.benefit = util.GetBenefit(this)
    } return this.benefit;
  } set Benefit(v){
    this.benefit = v;
  }

  description
  get Description(){
    if (!this.description){
      this.description = util.GetDescription(this)
    } return this.description;
  } set Description(v){
    this.description = v;
  }

  participant
  get Participant(){
    if (!this.participant){
      this.participant = util.GetParticipant(this)
    } return this.participant;
  } set Participant(v){
    this.participant = v;
  }
}