import { makeObservable, observable, action } from 'mobx';
import * as util from './InfoVMUtil'

export class InfoVM {
  constructor() {
    makeObservable(this, {

    })
  }

  brief
  get Brief(){
    if (!this.brief){
      this.brief = util.GetBrief(this)
    } return this.brief;
  }

  benefit
  get Benefit(){
    if (!this.benefit){
      this.benefit = util.GetBenefit(this)
    } return this.benefit;
  }

  description
  get Description(){
    if (!this.description){
      this.description = util.GetDescription(this)
    } return this.description
  }

  participant
  get Participant(){
    if (!this.participant){
      this.participant = util.GetParticipant(this)
    } return this.participant
  }
}