import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Popover } from 'antd';
import './Benefit.css'
import { BenefitVM } from './BenefitVM';

class BenefitView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new BenefitVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='cibe__wrapper flex'>
          <div className='cibe__dec'>
            <h3>{data.Header}</h3>
            {data.ListView.View}
          </div>
        </div>
      </>
    );
  }
}

export default observer(BenefitView);
