import { makeObservable, observable, action } from 'mobx';
import * as util from './CourseDictVMUtil'
import * as genUtil from '../../../../../util/generalUtil'

export class CourseDictVM {
  constructor() {
    makeObservable(this, {
      
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Item
  OnSet_Items_Func_Dict = []

  items
  get Items(){
    if (!this.items){
      util.Handle_Items(this)
    } return this.items
  } set Items(v){
    this.items = v;
    genUtil.Handle(this.OnSet_Items_Func_Dict,
      (v) => this.OnSet_Items_Func_Dict = v);
  }

  renderItem
  get RenderItem(){
    if (!this.renderItem){
      this.renderItem = util.GetDefault_RenderItem(this)
    } return this.renderItem
  } set RenderItem(v){
    this.renderItem = v;
  }
}