import { makeObservable, observable, action } from 'mobx';
import * as util from './UserVMUtil'
import './User.css'

export class UserVM {
  constructor() {
    makeObservable(this, {
      
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  get Name(){
    return this.ModelItem.Name
  }

  // View
  get BadgeView(){
    return <div key={this.id} 
      className='user__badge__wrapper dark-bgcolor'>
      {this.Name}
    </div>
  }
}