import { makeObservable, observable, action } from 'mobx';
import * as util from './ProductVMUtil'
import Product_BriefView from './Product_BriefView';
import Product_FullView from './Product_FullView';
import * as dateUtil from '../../../../../../util/dateUtil'

export class ProductVM {
  constructor() {
    makeObservable(this, {

    })
  }

  get id() {
    return this.ModelItem.id;
  }

  get Title() {
    return this.ModelItem.Title
  } set Title(v) {
    this.ModelItem.Title = v;
  }

  get Description() {
    return this.ModelItem.Description
  } set Description(v) {
    this.ModelItem.Description = v;
  }

  get CreatedDate(){
    return this.ModelItem.CreatedDate
  } set CreatedDate(v){
    this.ModelItem.CreatedDate = v;
  }

  get CreatedDate_String(){
    return dateUtil.ConvertToString(this.CreatedDate, 'dd/MM/yyyy')
  }

  authors
  get Authors() {
    if (!this.authors) {
      this.authors = util.GetAuthors(this)
    } return this.authors
  }

  instructors
  get Instructors() {
    if (!this.instructors) {
      this.instructors = util.GetInstructors(this)
    } return this.instructors
  }

  // Active
  SetActive = () => {
    if (this.Dict) {
      this.Dict.CurrentItem = this
    }
  }

  get ActiveCss() {
    return this.Dict?.CurrentItem === this ? 'active' : ''
  }

  // Event
  OnClick = () => {
    this.SetActive();
  }

  // View
  get BriefView() {
    return <Product_BriefView key={this.id} store={this} />
  }

  get FullView() {
    return <Product_FullView key={this.id} store={this} />
  }
}