import { message } from 'antd';
import * as genUtil from '../../util/generalUtil'
import * as util from './ListViewUtil'
import { makeObservable, observable, action } from 'mobx';
import './ListView.css'
import * as ot from './ListView_OrientationType'

export class ListView {
  constructor() {
    makeObservable(this, {
      Items: observable,
      ColumnCount: observable
    })
  }

  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Items = []

  get ItemCount() {
    return this.Items.length
  }

  ColumnCount = 1

  get RowCount() {
    return Math.ceil(this.ItemCount / this.ColumnCount);
  }

  Orientation = ot.horizontal

  idRender
  get IdRender() {
    if (!this.idRender) {
      this.idRender = util.GetDefault_IdRender(this)
    } return this.idRender
  } set IdRender(v) {
    this.idRender = v
  }

  itemRender;
  get ItemRender() {
    if (!this.itemRender) {
      this.itemRender = util.GetDefault_ItemRender(this)
    } return this.itemRender
  } set ItemRender(v) {
    this.itemRender = v
  }

  view_Render
  get View_Render() {
    if (!this.view_Render) {
      this.view_Render = util.GetDefault_View_Render(this)
    } return this.view_Render
  } set View_Render(v) {
    this.view_Render = v;
  }

  // View
  Wrapper_ClassName = ''
  Table_ClassName = ''

  get SingleColumn_View() {
    return this.Items?.map(this.ItemRender)
  }

  get MultiColumn_View() {
    return util.GetMultiColumn_View(this)
  }

  get MainView() {
    return this.ColumnCount === 1 ?
      this.SingleColumn_View : this.MultiColumn_View
  }

  get View() {
    return this.View_Render();
  }
}