import { message } from 'antd';
import * as genUtil from '../../../../../util/generalUtil'
import { makeObservable, observable, action } from 'mobx';
import { PMDataVM } from '../../../single/form/PMDataVM';
import * as vt from '../../view/ViewType'
import CourseDictView from '../../course/dict/CourseDictView';
import CourseInfoView from '../../course/view/info/CourseInfoView';

const pmDataVM = () => PMDataVM.Instance

export class Body {
  constructor() {
    makeObservable(this, {

    })
  }

  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // Data
  get CourseDict() {
    return pmDataVM().CourseDict;
  }

  get CurrentCourse() {
    return pmDataVM().CurrentCourse;
  }

  // View
  get MainView() {
    var qI = pmDataVM();
    if (!qI.RouteData) {
      qI.RouteData = [vt.home]
    }

    var rd = qI.RouteData
    switch (rd[0]) {
      case vt.home:
        return <div className='f-jc'>
          <div className='w80'>
            <CourseDictView store={this.CourseDict} />
          </div>
        </div>
      case vt.course:
        {
          switch (rd[1]) {
            case 'info':
              return this.CurrentCourse.InfoView
            case 'main':
              return <div className='f-jc'>
                <div className='w80'>
                  {this.CurrentCourse.Main.View}
                </div>
              </div>
          }
        }
    }
  }
}