import * as util from './PMDataUtil'
import * as genUtil from '../../../../util/generalUtil'

export class PMData {
  constructor() {

  }

  static instance
  static get Instance() {
    if (!this.instance) {
      this.instance = util.get();
    }
    return this.instance;
  } static set Instance(v) {
    this.instance = v;
  }

  // User
  OnSet_IsAdmin_Func_Dict = []

  isAdmin = false
  get IsAdmin() {
    return this.isAdmin
  } set IsAdmin(v) {
    this.isAdmin = v;
    genUtil.Handle(this.OnSet_IsAdmin_Func_Dict,
      (v) => this.OnSet_IsAdmin_Func_Dict = v)
  }

  // Deploy
  IsDeploy = false

  get Prefix_Link() {
    return this.IsDeploy ? '/bimdev2/#' : ''
  }

  // Data
  courseDict
  get CourseDict() {
    if (!this.courseDict) {
      this.courseDict = util.GetCourseDict(this)
    } return this.courseDict
  }
}