import { CheckOutlined } from "@ant-design/icons";
import { ListView } from "../../../../../listview/ListView";
import * as ot from "../../../../../listview/ListView_OrientationType";
import { BenefitVM } from "./BenefitVM";

// Instance
export const get = (q, dict) => {
  if (!q) return q;
  var qI = new BenefitVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetListView = (q) => {
  var qI = new ListView();

  qI.IdRender = (v) => v;
  qI.ItemRender = (v) => {
    return <div className='cibe__item__wrapper'>
      <CheckOutlined className='cibe__item__icon' />
      <span>{v}</span>
    </div>
  }

  qI.Orientation = ot.vertical
  qI.Table_ClassName = 'cibe__table'

  qI.Items = q.Items;
  qI.ColumnCount = 2;

  return qI;
}