import * as api from '../../../../../../util/apiUtil'
import { Benefit } from "../benefit/Benefit";
import { Brief } from "../brief/Brief";
import { Description } from "../description/Description";
import { Participant } from "../participant/Participant";

import * as benefitUtil from '../benefit/BenefitUtil'
import * as briefUtil from '../brief/BriefUtil'
import * as descriptionUtil from '../description/DescriptionUtil'
import * as participantUtil from '../participant/ParticipantUtil'

import { Info } from "./Info"
import { url } from './InfoUrl'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = new Info();

  qI._id = data._id;

  qI.Brief = briefUtil.GetFromDb(data.brief, qI)
  qI.Benefit = benefitUtil.GetFromDb(data.benefit, qI)
  qI.Description = descriptionUtil.GetFromDb(data.description, qI)
  qI.Participant = participantUtil.GetFromDb(data.participant, qI)

  qI.Dict = dict;

  return qI;
}

export const get = (dict) => {
  var qI = new Info();

  qI.Dict = dict;

  return qI;
}

// Property
export const GetBrief = (q) => {
  var qI = new Brief();
  qI.Dict = q;
  return qI;
}

export const GetBenefit = (q) => {
  var qI = new Benefit();
  qI.Dict = q;
  return qI;
}

export const GetDescription = (q) => {
  var qI = new Description();
  qI.Dict = q;
  return qI;
}

export const GetParticipant = (q) => {
  var qI = new Participant();
  qI.Dict = q;
  return qI;
}

// api
export const GetData = (apiData, handle) => {
  apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}