import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Popover } from 'antd';
import './Brief.css'
import { BriefVM } from './BriefVM';

class BriefView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new BriefVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='cib__wrapper'>
          {/* Background */}
          <div className='sec-bgcolor'>
            <div className='flex-center'>
              {/* Title */}
              <div className='cib__title__dec1'>
                <div className='cib__title__dec2'>
                  <h2 className='cib__h2'>{data.Name}</h2>
                  <span className='special'>{data.Description}</span>
                </div>

                {/* Addition */}
                <div className='cib__addition'>
                  {data.Dict.Dict.CostView}
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default observer(BriefView);
