import { Component } from "react";
import { observer } from 'mobx-react';
import { Dict } from './Dict'

class DictView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new Dict();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className={data.WrapperCss}>
          {data.Items?.map(x => x.View)}
        </div>
      </>
    )
  }
}

export default observer(DictView)