import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Popover } from 'antd';
import './CourseInfo.css'
import { CourseVM } from '../../base/CourseVM';
import { Link } from 'react-router-dom';
import InfoView from '../../info/base/InfoView';

class CourseInfoView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new CourseVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='couseinfo__wrapper'>
          {data.Info ?
            <InfoView store={data.Info} />
            : '' }
        </div>
      </>
    );
  }
}

export default observer(CourseInfoView);
