import { Component } from "react";
import { observer } from 'mobx-react';
import { Item } from "./Item";
import './Item.css'

class ItemView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new Item();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div onClick={data.OnClick}
          className={`nav__item ${data.Dict.ItemCss} ${data.ActiveCss}`}>{data.Title}</div>
      </>
    )
  }
}

export default observer(ItemView)