import { makeObservable, observable, action } from 'mobx';
import * as util from './CourseVMUtil'
import * as genUtil from '../../../../../util/generalUtil'
import ImageView from '../../../../image/ImageView'
import * as imgUtil from '../../../../image/ImageUtil'
import CourseCostView from '../view/cost/CourseCostView';
import CourseInfoView from '../view/info/CourseInfoView';

export class CourseVM {
  constructor() {
    makeObservable(this, {
      info: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  get Meta() {
    return this.ModelItem.Meta;
  } set Meta(v) {
    this.ModelItem.Meta = v;
  }

  // Link
  get SupLink() {
    return `course/${this.Meta}`
  }

  get InfoLink() {
    return `${this.SupLink}/info`
  }

  get MainLink() {
    return `${this.SupLink}/main`
  }

  get Name() {
    return this.ModelItem.Name;
  } set Name(v) {
    this.ModelItem.Name = v;
  }

  // Thumbnail
  get Thumbnail() {
    return this.ModelItem.Thumbnail;
  } set Thumbnail(v) {
    this.ModelItem.Thumbnail = v;
  }

  get ThumbnailView() {
    var img = imgUtil.get(this.Thumbnail);
    return <ImageView store={img} />
  }

  // Info
  OnSet_Info_Func = []

  info = undefined
  get Info() {
    if (this.info === undefined) {
      util.Handle_Info(this)
    } return this.info
  } set Info(v) {
    this.info = v;
  }

  // Cost
  cost
  get Cost() {
    if (!this.cost) {
      this.cost = util.GetCost(this)
    } return this.cost
  }

  // ProductDict
  productDict
  get ProductDict(){
    if (!this.productDict){
      this.productDict = util.GetProductDict(this)
    } return this.productDict
  }

  // main
  main
  get Main(){
    if (!this.main){
      this.main = util.GetMain(this)
    } return this.main;
  }

  // View
  get InfoView() {
    return <CourseInfoView key={this.id} store={this} />
  }

  get CostView() {
    return <CourseCostView store={this} />
  }
}