import { ListView } from "../../../../../listview/ListView";
import * as ot from "../../../../../listview/ListView_OrientationType";
import PointerView from "../../../../../svg/PointerView";
import { ParticipantVM } from "./ParticipantVM";

// Instance
export const get = (q, dict) => {
  if (!q) return q;
  var qI = new ParticipantVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetListView = (q) => {
  var qI = new ListView();
  var iconSize = 15

  qI.ItemRender = (v) => <div className='cip__item__wrapper' key={v}>
    <span>
      <PointerView width={iconSize} height={iconSize}
        className='cip__item__icon'/>
    </span>
    <span>{v}</span>
  </div>

  qI.Items = q.Items;
  qI.ColumnCount = 1;

  return qI;
}