import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Popover } from 'antd';
import './Participant.css'
import { ParticipantVM } from './ParticipantVM';

class ParticipantView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new ParticipantVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='cip__wrapper'>
          <h3>{data.Header}</h3>
          {data.ListView.View}
        </div>
      </>
    );
  }
}

export default observer(ParticipantView);
