import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { MainVM } from './MainVM';
import './Main.css'
import * as vt from './Main_ViewType'

class MainView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MainVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='f'>
          <div>
            {data.Menu.View}
          </div>

          <div className='f1 cm__main__wrapper'>
            {data.ViewType === vt.content ? "Content" :
              data.Dict.ProductDict.View
            }
          </div>
        </div>
      </>
    );
  }
}

export default observer(MainView);
