import { makeObservable, observable, action } from 'mobx';
import DictView from "./DictView"

export class Dict {
  constructor() {
    makeObservable(this, {
      Items: observable,
      CurrentItem: observable
    })
  }

  WrapperCss = ''

  ItemCss = ''

  Items = undefined

  CurrentItem = undefined

  get View() {
    return <DictView store={this} />
  }
}