import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import './Header.css'
import { Header } from './Header';
import { Link } from 'react-router-dom';

class HeaderView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new Header()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='header__wrapper flex-center'>
          <div className='w80 flex-start'>
            <Link to='/' className='header__logo__wrapper'>
              {data.LogoView}
            </Link>
          </div>
        </div>
      </>
    );
  }
}

export default observer(HeaderView);
