import { makeObservable, observable, action } from 'mobx';
import DictView from './DictView';
import * as util from './DictVMUtil'

export class DictVM {
  constructor() {
    makeObservable(this, {
      items: observable,
      CurrentItem : observable
    })
  }

  items = undefined
  get Items(){
    if (!this.items){
      this.items = util.GetItems(this)
    } return this.items;
  }

  CurrentItem = undefined

  get View(){
    return <DictView store={this}/>
  }
}