import { message } from "antd";
import * as genUtil from '../../util/generalUtil'
import * as ot from './ListView_OrientationType'

// Instance


// Property
export const GetDefault_IdRender = (q) => {
  return (v) => v.id
}

export const GetDefault_ItemRender = (q) => {
  return (v) => <div key={q.IdRender(v)}>{v}</div>
}

export const GetMultiColumn_View = (q) => {
  var rc = q.RowCount;
  var cc = q.ColumnCount;
  var ic = q.ItemCount;
  var list = q.Items;

  var elem = []
  var getIndex

  switch (q.Orientation) {
    case ot.horizontal:
      getIndex = (r, c) => r * cc + c;
      break;
    case ot.vertical:
      getIndex = (r, c) => r + c * rc;
      break;
  }

  for (let r = 0; r < rc; r++) {
    var elem2 = []
    for (let c = 0; c < cc; c++) {
      var index = getIndex(r, c);
      if (index < ic) {
        var obj = list[index]
        elem2.push(
          <td key={q.IdRender(obj)}>{q.ItemRender(obj)}</td>
        )
      }
    }
    elem.push(
      <tr key={genUtil.randomId()}>{elem2}</tr>
    )
  }

  return <table className={q.Table_ClassName}>
    <tbody>
      {elem}
    </tbody>
  </table>
}

export const GetDefault_View_Render = (q) => {
  return () => <div className={q.Wrapper_ClassName}>
    {q.MainView}
  </div>
}