import { InfoVM } from "./InfoVM";
import * as benefitUtil from '../benefit/BenefitVMUtil'
import * as briefUtil from '../brief/BriefVMUtil'
import * as descriptionUtil from '../description/DescriptionVMUtil'
import * as participantUtil from '../participant/ParticipantVMUtil'

// Instance
export const get = (q, dict) => {
  if (!q) return q;
  var qI = new InfoVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetBrief = (q) => {
  return briefUtil.get(q.ModelItem.Brief, q)
}

export const GetBenefit = (q) => {
  return benefitUtil.get(q.ModelItem.Benefit, q)
}

export const GetDescription = (q) => {
  return descriptionUtil.get(q.ModelItem.Description, q)
}

export const GetParticipant = (q) => {
  return participantUtil.get
    (q.ModelItem.Participant, q)
}