import { Dict } from './Dict'
import * as itemUtil from '../base/ProductUtil'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id;

  return qI;
}

export const get = (dict) => {
  var qI = new Dict();

  qI.Dict = dict;

  return qI;
}

export const GetTest = (dict) => {
  var qI = get(dict);

  qI.Items = [
    itemUtil.GetTest(dict),
    itemUtil.GetTest1(dict)
  ]

  return qI;
}

// Property
