import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Popover } from 'antd';
import './CourseItem.css'
import { CourseVM } from '../../base/CourseVM';
import { Link } from 'react-router-dom';

class CourseItemView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new CourseVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <Link to={data.InfoLink}>
          <div className='courseitem__wrapper'>
            {data.ThumbnailView}
            <div className='couseitem__name__wrapper flex-center'>
              {data.Name}
            </div>
          </div>
        </Link>
      </>
    );
  }
}

export default observer(CourseItemView);
