import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Popover } from 'antd';
import './Cost.css'
import { CostVM } from './CostVM';
import { CaretDownOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';

class CostView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new CostVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='cc__wrapper'>
          <span className='cc__realvalue main-color'>
            {data.FormatCurrency(data.RealValue)}
          </span>
          <div className='cc__rawvalue__wrapper dark-incolor'>
            <span className='cc__rawvalue'>{data.FormatCurrency(data.RawValue)}</span>
            <span className='cc__discount main-color'>
              <span>
                <CaretDownOutlined className='cc__discount__icon' />
              </span>
              <span>{data.FormatDiscount(data.Discount)}</span>
            </span>
          </div>

          <div className='cc__button__wrapper'>
            <Button type='primary' size='small'
              className='cc__button main-bgcolor'>
              THM GI HNG
            </Button>

            <Link to="main">
              <Button type='primary' size='small'
                className='cc__button sec-bgcolor'>
                XEM NI DUNG
              </Button>
            </Link>
          </div>
        </div>
      </>
    );
  }
}

export default observer(CostView);
