import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Popover } from 'antd';
import './Description.css'
import { DescriptionVM } from './DescriptionVM';
import HtmlRenderView from '../../../../../html/HtmlRenderView';

class DescriptionView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new DescriptionVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='cid__wrapper'>
          <h3>{data.Header}</h3>
          <HtmlRenderView store={data.HtmlContentVM} />
        </div>
      </>
    );
  }
}

export default observer(DescriptionView);
