// Instance
export const GetFromDb = (data, dict) => {
  var qI = new Course();

  qI._id = data._id;
  qI.Name = data.name
  qI.Info = infoUtil.GetFromDb(data.infos[0], qI);
  qI.Dict = dict;

  return qI;
}

export const get = (dict) => {
  var qI = new Course();

  qI.Dict = dict;

  return qI;
}

