// Property
export const Handle_Info = (q) => {
  if (q.IsGet_Info) return undefined;
  q.IsGet_Info = true;

  const setObj = (data) => {
    q.Info = infoUtil.GetFromDb(data[0], q)
    q.IsGet_Info = false
  }

  const getApi = () => {
    var apiData = {
      dictId: q._id
    }

    infoUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }

  if (q._id){
    getApi()
  } else {
    genUtil.Add_2(
      q.OnSet_id_Func_Dict,
      () => getApi()
    )
  }
}

export const GetCost = (q) => {
  return costUtil.get(q)
}

export const GetProductDict = (q) => {
  return productDictUtil.get(q)
}

// api
export const GetData = (apiData, handle) => {
  apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}