import { PMDataVM } from "../../single/form/PMDataVM";
import * as genUtil from '../../../../util/generalUtil'

export const home = 'home'
export const course = 'course'

const pmDataVM = () => PMDataVM.instance

export const Handle = (type, meta, type2) => {
  var qI = pmDataVM();
  qI.RouteData = [type ? type : home, type2];

  switch (type) {
    case 'course':
      var list = qI.CourseDict.Items;
      qI.CurrentCourse = genUtil.SingleFilter(list, (x) => x.Meta === meta)
      break;
  }
}