import { Button } from "antd";
import { ProductVM } from "./ProductVM";
import * as userUtil from '../../../user/UserVMUtil'

// Instance
export const get = (q, dict) => {
  if (!q) return q;
  var qI = new ProductVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetAuthors = (q) => {
  return q.ModelItem.Authors.map(x => userUtil.get(x, q))
}

export const GetInstructors = (q) => {
  return q.ModelItem.Instructors.map(x => userUtil.get(x, q))
}