import { CD2_Class, Course, CourseDict } from './CourseDict'
import * as genUtil from '../../../../../util/generalUtil'
import * as iUtil from '../base/CourseUtil'

// Instance
export const get = (dict) => {
  var qI = new CourseDict();

  qI.Dict = dict;

  return qI;
}

export const GetTest = (dict) => {
  var qI = get(dict)

  qI.Items = [
    iUtil.GetTest(qI)
  ]
  
  return qI;
}

// Property
// Item
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    var list = data.map(x => iUtil.GetFromDb(x, q))

    q.Items = list;
    q.IsGet_Items= false
  }

  var apiData = {
    
  }

  iUtil.GetData(apiData, (data) => {
    setObj(data)
  })
}