import { message } from 'antd';
import * as genUtil from '../../../../../util/generalUtil'
import * as util from './CourseUtil'
import { url } from './CourseUrl'

export class Course {
  OnSet_id_Func_Dict = []
  __id
  get _id(){
    return this.__id
  } set _id(v){
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Meta

  name
  get Name() {
    if (!this.name) {
      this.name = 'class name'
    }
    return this.name;
  } set Name(v) {
    this.name = v;
  }

  Thumbnail

  // Info
  OnSet_Info_Func = []

  info
  get Info(){
    if (this.info === undefined){
      util.Handle_Info(this)
    } return this.info
  } set Info(v){
    this.info = v;
    genUtil.Handle(this.OnSet_Info_Func,
      (v) => this.OnSet_Info_Func = v)
  }

  // Cost
  cost
  get Cost(){
    if (!this.cost){
      this.cost = util.GetCost(this)
    } return this.cost
  } set Cost(v){
    this.cost = v
  }

  // ProductDict
  productDict
  get ProductDict(){
    if (!this.productDict){
      this.productDict = util.GetProductDict(this)
    } return this.productDict
  } set ProductDict(v){
    this.productDict = v;
  }
}