import { message } from 'antd';
import * as genUtil from '../../../../util/generalUtil'
import * as util from './UserUtil'
import { url } from './User_Url'

export class User {
  OnSet_id_Func_Dict = []
  __id
  get _id(){
    return this.__id
  } set _id(v){
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Name

  Gmail
}