import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { DictVM } from './DictVM';
import './Dict.css'

class DictView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new DictVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.CurrentItem ?
          <div className='cpd__currentitem__warpper'>
            {data.CurrentItem.FullView}
          </div>
          : ''
        }
        {data.Items.map(x => x.BriefView)}
      </>
    );
  }
}

export default observer(DictView);
