import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { ProductVM } from './ProductVM';
import './Product_Full.css'

class Product_FullView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new ProductVM()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div>
          <div className='f-jc'>
            <div className='f cpp_t__wrapper'>
              <span className='cpp__t__title sec-color'>D N:</span>
              <span className='cpp__t__value'>{data.Title}</span>
            </div>
          </div>

          <table className='cpp__fieldtable'>
            <tr>
              <td className='cpp__field__title'>Tn d n:</td>
              <td className='cpp__field__input'>{data.Title}</td>
            </tr>
            <tr>
              <td className='cpp__field__title'>M t:</td>
              <td className='cpp__field__input'>{data.Description}</td>
            </tr>
            <tr>
              <td className='cpp__field__title'>Ngy to:</td>
              <td className='cpp__field__input'>{data.CreatedDate_String}</td>
            </tr>
          </table>
        </div>
      </>
    );
  }
}

export default observer(Product_FullView);
