import * as api from '../../../../../../util/apiUtil'
import { Product } from './Product'
import { url } from './ProductUrl'
import * as userUtil from '../../../user/UserUtil'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id;

  return qI;
}

export const get = (dict) => {
  var qI = new Product();

  qI.Dict = dict;

  return qI;
}

export const GetTest = (dict) => {
  var qI = get(dict);

  qI.Title = 'To m chi tit lin kt'
  qI.Description = 'Vo family, edit bng m bng tham bin Comments'

  qI.CreatedDate = new Date();

  qI.Authors = [ userUtil.GetTest(dict) ]
  qI.Instructors = [
    userUtil.GetTest1(dict),
    userUtil.GetTest2(dict)
  ]

  return qI;
}

export const GetTest1 = (dict) => {
  var qI = get(dict);

  qI.Title = 'V thp theo cnh c sn'
  qI.Description = 'Vo family, edit bng m bng tham bin Comments'
  
  qI.CreatedDate = new Date();

  qI.Authors = [ userUtil.GetTest3(dict) ]
  qI.Instructors = [
    userUtil.GetTest1(dict),
    userUtil.GetTest2(dict)
  ]

  return qI;
}

// Property


// api
export const GetData = (apiData, handle) => {
  apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}