import { message } from 'antd';
import * as genUtil from '../../../../../util/generalUtil'
import * as util from './CourseDictUtil'

export class CourseDict {
  _id
  
  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // Item
  OnSet_Items_Func_Dict = []

  items
  get Items(){
    if (!this.items){
      util.Handle_Items(this)
    } return this.items
  } set Items(v){
    this.items = v;
    genUtil.Handle(this.OnSet_Items_Func_Dict,
      (v) => this.OnSet_Items_Func_Dict = v);
  }
}