import * as api from '../../../../util/apiUtil'
import { PMData } from '../model/PMData';
import { PMDataVM } from './PMDataVM';
import * as genUtil from '../../../../util/generalUtil'
import * as layoutUtil from '../../entity/layout/layout/LayoutUtil'
import * as courseDictUtil from '../../entity/course/dict/CourseDictVMUtil'

export const pmData = () => PMData.Instance;

// Instance
export const get = () => {
  var qI = new PMDataVM();

  genUtil.Add(pmData().OnSet_IsAdmin_Func_Dict, () => {
    // Update nav
    qI.IsAdmin = pmData().IsAdmin
  }, false)

  return qI;
}

// Property
export const GetLayout = (q) => {
  return layoutUtil.get()
}

export const GetCourseDict = (q) => {
  return courseDictUtil.get(pmData().CourseDict, q)
}