import HtmlRenderVM from "../../../../../html/HtmlRenderVM";
import { DescriptionVM } from "./DescriptionVM";

// Instance
export const get = (q, dict) => {
  if (!q) return q;
  var qI = new DescriptionVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetHtmlContentVM = (q) => {
  var qI = new HtmlRenderVM();
  qI.Content = q.HtmlContent;
  return qI;
}