import { message } from 'antd';
import * as genUtil from '../../../../../../util/generalUtil'
import MenuView from './MenuView';
import * as util from './MenuVMUtil'

export class MenuVM {
  itemDict
  get ItemDict() {
    if (!this.itemDict) {
      this.itemDict = util.GetItemDict(this)
      util.SetItems(this)
    } return this.itemDict;
  }

  items
  get Items() {
    if (!this.items) {
      this.items = util.GetItems(this)
    } return this.items;
  }

  get View() {
    return <MenuView store={this} />
  }
}